const format = (a, parent) => {
  if ('number' === a?.type) {
    return '' + a?.value; // + (a?.unit ? a?.unit : '');
  } else if ('comparable' === a?.type) {
    if (Infinity === a?.value) {
      return 'infinity';
    } else if (-Infinity === a?.value) {
      return '-infinity';
    }
  } else if ('bool' === a?.type) {
    return '' + a?.value;
  } else if ('text' === a?.type) {
    if (parent) {
      return `'${a?.value}'`;
    } else {
      return a?.value;
    }
  } else if ('variable' === a?.type) {
    return a?.value;
  } else if ('list' === a?.type) {
    if (a?.dotList) {
      return `.${a?.value?.map((x) => x.value)?.join('.')}`;
    } else {
      const data = a?.value?.map((x) => format(x, 'list'));

      if (data?.join(' ').length > lineTooLong) {
        // tap('too long');
        return `[\n${addIndention(data)}\n]`;
      } else {
        return `[ ${data?.join(' ')} ]`;
      }
    }
  } else if ('map' === a?.type) {
    const data = Object.entries(a?.value)?.map(
      ([k, v]) => `${k} ${format(v, 'map')}`
    );

    if (data?.join(' ').length > lineTooLong) {
      return `{\n${addIndention(data)}\n}`;
    } else {
      return `{ ${data?.join(' ')} }`;
    }
  } else if ('parens' === a?.type) {
    if (a?.value?.[0]?.value === 'file') {
      return splitEveryTwo(
        a?.value?.slice(1).map((x) => format(x, 'parens'))
      )
        .map((x) => x.join(' '))
        .join('\n\n');
    } else if (a?.value?.[0]?.value === '#') {
      return `(# ${a?.value
        ?.slice(1)
        ?.map((x) => format(x))
        // ?.filter((x) => '#' !== x)
        ?.join(' ')})`;
    } else if (a?.value?.[0]?.value === '[]') {
      return '%' + format(makeList(a?.value?.slice(1)));
    } else if (a?.value?.[0]?.value === '{}') {
      return '%' + format(makeMap(a?.value?.slice(1)));
    } else if (a?.value?.[0]?.value === 'dotget') {
      return `${format(a?.value?.[1])}${format(
        a?.value?.[2]
      )}`;
    } else {
      return `(${a?.value
        ?.map((x) => format(x, 'parens'))
        ?.join(' ')})`;
    }
  } else if ('error' === a?.type) {
    return `ERROR: ${a?.value}`;
  } else if (undefined === a || isNaN(a)) {
    return 'empty';
  }
};

const tokens = (code) => {
  let tokens = [];
  let charGroup = '';
  const codeSplit = code.split('');

  for (let i = 0; i < codeSplit.length; i++) {
    const char = codeSplit?.[i];

    if (
      '\\' === char &&
      ['(', ')', "'", '\\'].includes(codeSplit?.[i + 1])
    ) {
      charGroup += codeSplit?.[i + 1];
      i++;
    } else if (
      [
        '(',
        ')',
        '[',
        ']',
        '{',
        '}',
        '\n',
        "'",
        ' ',
      ].includes(char)
    ) {
      if (charGroup) {
        if ('@' === charGroup?.[0]) {
          tokens.push('@');
          if (charGroup.slice(1)) {
            tokens.push(charGroup.slice(1));
          }
        } else if ('_' === charGroup?.[0]) {
          tokens.push('event');
          if (charGroup.slice(1)) {
            tokens.push(charGroup.slice(1));
          }
        } else if ('...' === charGroup.slice(0, 3)) {
          tokens.push('...');
          if (charGroup.slice(3, Infinity)) {
            tokens.push(charGroup.slice(3, Infinity));
          }
        } else {
          tokens.push(charGroup);
        }
        charGroup = '';
      }
      if ([' ', '\n'].includes(char)) {
        tokens.push(char);
      } else {
        tokens.push({ bracket: char });
      }
    } else {
      charGroup += char;
    }
  }

  return tokens;
};

const nestTokens = (tokens) => {
  let groups = [[]];
  let drop = 0;
  let dropLength = -1;

  for (let i = 0; i < tokens?.length; i++) {
    const token = tokens[i];

    if (1 === drop && dropLength === groups?.length) {
      drop++;
    } else if (
      2 === drop &&
      dropLength === groups?.length
    ) {
      const pop = groups.pop();

      last(groups).push(pop);
      drop = 0;
      dropLength = -1;
    }

    if (
      "'" === token?.bracket &&
      last(groups)?.[0] !== "'"
    ) {
      groups.push(["'"]);
    } else if ('(' === token?.bracket) {
      groups.push([]);
    } else if ('[' === token?.bracket) {
      groups.push(['[]']);
    } else if ('{' === token?.bracket) {
      groups.push(['{}']);
    } else if (['@', '...'].includes(token)) {
      groups.push(['@']);
      dropLength = groups?.length;
      drop = 1;
    } else if (
      [')', '}', ']', "'"].includes(token?.bracket)
    ) {
      const pop = groups.pop();

      last(groups).push(pop);
    } else if ([' ', '\n'].includes(token)) {
      if (first(last(groups)) === "'") {
        last(groups).push(token);
      }
    } else {
      last(groups).push(token);
    }

    // console.log(groups);
  }

  return groups?.[0]?.[0];
};

const parseText = (nestedTokens) => {
  let text = '';
  let group = [variableType('++')];
  let concat = false;

  for (let pa of nestedTokens.slice(1)) {
    if (Array.isArray(pa)) {
      concat = true;
      group.push(textType(text));
      group.push(ast(pa));
      text = '';
    } else {
      text += pa;
      // (text ? ' ' : '') +
    }
  }

  group.push(textType(text));

  return concat ? parensType(group) : textType(text);
};

const ast = (nestedTokens) => {
  if (Array.isArray(nestedTokens)) {
    if ("'" === nestedTokens?.[0]) {
      return parseText(nestedTokens);
    } else if ('@' === nestedTokens?.[0]) {
      return dropType(ast(nestedTokens?.[1]));
    } else if ('#' === nestedTokens?.[0]) {
      const middle = lefts(rights(nestedTokens)).map(ast);
      const hashIndex = middle.findIndex(
        (x) => '#' === x?.value
      );

      if (-1 === hashIndex) {
        return parensType([
          variableType('#'),
          listType(middle),
          ast(last(nestedTokens)),
        ]);
      } else {
        return parensType([
          variableType('#'),
          listType(middle.slice(0, hashIndex)),
          parensType([
            variableType('let'),
            ...middle.slice(hashIndex + 1, Infinity),
            ast(last(nestedTokens)),
          ]),
        ]);
      }
    } else {
      return parensType(nestedTokens.map(ast));
    }
  } else if ('true' === nestedTokens) {
    return boolType(true);
  } else if ('false' === nestedTokens) {
    return boolType(false);
  } else if ('infinity' === nestedTokens) {
    return comparableType(Infinity);
  } else if ('-infinity' === nestedTokens) {
    return comparableType(-Infinity);
  } else if ('tau' === nestedTokens) {
    return numberType(Math.PI * 2);
  } else if ('pi' === nestedTokens) {
    return numberType(Math.PI);
  } else if ('euler' === nestedTokens) {
    return numberType(Math.E);
  } else if ('_time' === nestedTokens) {
    return mapType({ _time: listType([]) });
  } else if ('...' === nestedTokens) {
    return variableType('...');
  } else if (
    (+nestedTokens.replace(/_/g, '') ||
      0 === +nestedTokens.replace(/_/g, '')) &&
    '.' !== nestedTokens?.[0]
  ) {
    return numberType(nestedTokens.replace(/_/g, ''));
  } else if (/\./g.test(nestedTokens?.[0])) {
    return dotListType(
      nestedTokens.split('.')?.slice(1)?.map(textType)
    );
  } else if (
    !/[0-9]/.test(nestedTokens?.[0]) &&
    /\./g.test(nestedTokens)
  ) {
    const x = nestedTokens.split('.');

    return parensType([
      variableType('dotget'),
      variableType(x?.[0]),
      dotListType(x?.slice(1)?.map(textType)),
    ]);
  } else {
    return variableType(nestedTokens);
  }
};

const parse = (t) => {
  return ast(nestTokens(tokens(t?.replace(/;.+\n/g, ''))));
};

const deref = (code, vars) => {
  if ('variable' === code?.type && vars?.[code?.value]) {
    let v = vars?.[code?.value];
    while ('variable' === v?.type && vars?.[v?.value]) {
      v = vars?.[v?.value];
    }
    return v;
  } else {
    return code;
  }
};

const compute = (code, v) => {
  const vars = code?.moduleVars
    ? code?.moduleVars
    : { ...v, ...code?.vars };

  if (code?.type === 'parens') {
    const fn2 = deref(first(code?.value), vars);

    const fn =
      'parens' === fn2?.type &&
      'dotget' === fn2?.value?.[0]?.value
        ? compute(fn2, vars)
        : fn2;

    if ('parens' === fn?.type) {
      return compute(
        parensType([
          first(fn?.value),
          ...rights(code?.value),
          ...rights(fn?.value),
        ]),
        fn?.moduleVars
          ? fn?.moduleVars
          : { ...vars, ...fn?.vars }
      );
    } else if ('list' === fn?.type && fn?.dotList) {
      if (rights(code?.value)?.length >= 2) {
        const args = rights(code?.value)?.reverse();

        return compute(
          parensType([
            variableType('set'),
            args?.[1],
            fn,
            args?.[0],
          ]),
          vars
        );
      } else {
        return code;
      }
    } else if (
      'variable' === fn?.type &&
      core?.[fn?.value]
    ) {
      const c = core?.[fn?.value];

      const args = rights(code?.value).flatMap((arg) => {
        if (c?.meta) {
          return arg;
        } else {
          const a = compute(arg, vars);
          if ('drop' === a?.type) {
            const b = compute(a?.value, vars);
            if ('list' === b?.type) {
              return b?.value;
            } else {
              return b;
            }
          } else {
            return a;
          }
        }
      });

      if (args?.length >= c.argTypes?.length) {
        return c?.fn?.(c?.reverse ? args.reverse() : args, {
          ...vars,
        });
      } else {
        return code;
      }
    } else {
      return fn;
    }
  } else if (
    'variable' === code?.type &&
    vars?.[code?.value]
  ) {
    return compute(deref(code, vars), vars);
  } else {
    return code;
  }
};

