const effects = {
  random: {
    fn: ([b, a], p, k, cb) => {
      tap('bbb', b, a);

      const aa = jsBool(a?.value) ? a?.value : 1;

      p[k] = numberType(
        Math.floor(Math.random() * (b?.value - aa + 1) + aa)
      );

      cb();
    },
  },
  time: {
    fn: ([], p, k, cb) => {
      p[k] = textType(new Date().toISOString());
    },
  },
  timer: {
    fn: ([b, a], p, k, cb) => {
      setTimeout(() => {
        p[k] = boolType(true);

        cb();

        setState(
          parensType([module?.value?.['$' + b?.value], $])
        );
      }, (a?.value ?? 1) * 1000);
    },
  },
  interval: {
    fn: ([b, a], p, k, cb) => {
      setInterval(() => {
        p[k] = boolType(true);

        cb();

        setState(
          parensType([module?.value?.['$' + b?.value], $])
        );
      }, (a?.value ?? 1) * 1000);
    },
  },
  alert: {
    fn: ([a], p, k, cb) => {
      p[k] = boolType(true);

      alert(a?.value);

      cb();
    },
  },
  identity: {
    fn: ([a], p, k, cb) => {
      cb();
    },
  },
};

const localUser = ($key, key, args, parent, upKey) => {
  if (module?.value?.[$key]) {
    parent[upKey] = boolType(true);

    other();

    setState(
      parensType([module?.value?.[$key], ...args, $])
    );
  } else if (effects?.[key]) {
    effects?.[key]?.fn(args, parent, upKey, () => {
      other();
    });
  } else {
    socket.emit(key, args, (a) => {
      parent[upKey] = a;

      other();
    });
  }
};

const runEvents = (input, parent, upKey) => {
  if (input?.type === 'list') {
    for (let i = 0; i < input?.value.length; i++) {
      runEvents(input?.value[i], input?.value, i);
    }
  } else if (input?.type === 'map') {
    for (let $key in input?.value) {
      if (
        '$' === $key?.[0] &&
        input?.value[$key]?.value?.auth
      ) {
        const auth = input.value[$key].value.auth;
        delete input.value[$key].value.auth;

        const key = $key.slice(1);

        const args =
          input?.value[$key]?.value?.args?.value?.reverse();

        if (
          'local_user' ===
          input.value[$key]?.value?.type?.value
        ) {
          tap('2');
          localUser($key, key, args, parent, upKey);
        } else if (
          'client' === input.value[$key]?.value?.type?.value
        ) {
          socket.emit(
            'client',
            textType(key),
            args,
            auth,
            () => {}
          );
        } else if (
          'server' === input.value[$key]?.value?.type?.value
        ) {
          socket.emit(
            'server',
            textType(key),
            args,
            auth,
            () => {}
          );
        }
      } else {
        runEvents(input?.value[$key], input?.value, $key);
      }
    }
  }
};

const setState = (code, cb) => {
  $ = compute(code);

  runEvents($, {}, 'x');

  other();
};

let started = false;

globalThis.blocks = [];

const drawBlock = (
  block,
  _x,
  _y,
  _w,
  _h,
  parentOptions
) => {
  if ('list' === block?.type) {
    const has_options = 'map' === block?.value?.[0]?.type;
    const options = has_options
      ? block?.value?.[0]?.value
      : {};
    const bv = has_options
      ? block?.value?.slice(1)
      : block?.value;

    const x = options?.x ? options?.x?.value : _x;
    const y = options?.y ? options?.y?.value : _y;
    const w = options?.w ? options?.w?.value : _w;
    const h = options?.h ? options?.h?.value : _h;
    const padding = options?.p?.value ?? 0;
    const paddingv = options?.pv?.value ?? padding;
    const paddingh = options?.ph?.value ?? padding;
    const gap = options?.gap?.value ?? 0;
    const type = options?.t?.value;
    const border = options?.b?.value;
    const click = options?.click;

    if (click) {
      blocks.unshift({
        x,
        y,
        w,
        h,
        click,
      });
    }

    tap(block, x, y, w, h);

    if (border) {
      c.strokeStyle = 'black';
      c.lineWidth = border;
      if (options?.r) {
        c.beginPath();
        c.roundRect(x, y, w, h, options?.r?.value);
        c.stroke();
      } else {
        c.strokeRect(x, y, w, h);
      }
    }

    if ('content' === type) {
      let sectionY = 0;
      for (let b of bv) {
        sectionY += drawBlock(
          b,
          x + padding,
          y + sectionY + padding,
          w - padding * 2,
          h,
          options
        );
        tap(sectionY);
      }

      return h;
    } else if ('rows' === type) {
      const sectionH =
        (h - paddingv * 2 - gap * (bv?.length - 1)) /
        bv?.length;

      let sectionY = paddingv;
      for (let b of bv) {
        drawBlock(
          b,
          x + paddingh,
          y + sectionY,
          w - paddingh * 2,
          sectionH,
          options
        );

        sectionY += sectionH + gap;
      }

      return h;
    } else {
      const sectionW =
        (w - paddingh * 2 - gap * (bv?.length - 1)) /
        bv?.length;

      let sectionX = paddingh;
      for (let b of bv) {
        drawBlock(
          b,
          x + sectionX,
          y + paddingv,
          sectionW,
          h - paddingv * 2,
          options
        );

        sectionX += sectionW + gap;
      }

      return h;
    }
  } else {
    const s = parentOptions?.font_size
      ? parentOptions?.font_size?.value
      : 20;
    // window
    //     .getComputedStyle(document.body)
    //     .fontSize?.slice(0, -2);

    c.font = s + 'px Arial';
    tap('w', _w, c.measureText(block?.value)?.width);
    tap(parentOptions);
    tap(
      'kj',
      _w,
      _w / 2 - c.measureText(block?.value)?.width / 2 + _x,
      c.measureText(block?.value)?.width / 2,
      block?.value
    );
    const x =
      'center' === parentOptions?.hz?.value
        ? _w / 2 -
          c.measureText(block?.value)?.width / 2 +
          _x
        : 'right' === parentOptions?.hz?.value
        ? _w + _x - c.measureText(block?.value)?.width
        : _x;
    const y =
      'center' === parentOptions?.vt?.value
        ? _h / 2 - s + _y
        : _y;
    c.fillText(block?.value, x, y + s - 5);
    return 24;
  }
};

const view = () => {
  c.clearRect(0, 0, canvas.width, canvas.height);

  const lastView = compute(
    parensType([module?.value?.$view, $])
  );

  blocks = [];

  drawBlock(lastView, 0, 0, canvas.width, canvas.height);
};

const lastView = mapType({});

const other = () => {
  if ($?.value?.log) {
    console.log(format($?.value?.log));
    delete $?.value?.log;
  }

  if (module?.value?.$view) {
    view();
  }

  if (!started && module?.value?.$start && !stillMore($)) {
    started = true;
    setState(parensType([module?.value?.$start, $]));
  }
};

const stillMore = (input) => {
  if (input?.type === 'list') {
    let n = 0;

    for (let i = 0; i < input?.value.length; i++) {
      n += stillMore(input?.value[i]);
    }

    return n;
  } else if (input?.type === 'map') {
    let n = 0;

    for (let key in input?.value) {
      if ('$' === key?.[0]) {
        n++;
      } else {
        n += stillMore(input?.value[key]);
      }
    }

    return n;
  } else {
    return 0;
  }
};

